import {useRouter} from "next/router";
import React from "react";
import HeaderBar from "../../components/nav/HeaderBar";
//import { Typography } from "@material-ui/core/Typography";
import MakeVideoPlayer from "../../components/Posts/MakeVideoPlayer";
import GetVideos from "../../components/Videos/GetVideos";
import GetTrueVideoSource from "../../components/Posts/GetTrueVideoSource";

export async function getServerSideProps(context){
    let page = context.query.page;
    let videoArray = await GetVideos(page);
    const videos = videoArray.map( async (videoPost)=>{
        const video = videoPost.node.url;
        console.log("first source: ",video);
        const trueVideo = await GetTrueVideoSource(video);
        return trueVideo
    });

    const promisedVideos = Promise.all(videos);

    return{props: {promisedVideos}}
}

const Page = ({videos}) => {
    const router = useRouter();


    const videoList = videos.map((video)=>{
        return <MakeVideoPlayer source={video} />
    });

    return (
        <div>
            <HeaderBar/>
            <div>
                <div className='pagetitle'>
                    <p align='center' variant={'h4'}>{router.query.page}'s Videos</p>
                </div>
                <div>
                    {videoList}
                </div>
            </div>
        </div>
    );
};

export default Page;